import "./App.css";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Stats, Loader } from "@react-three/drei";
import Particles from "./Components/Particles/Particles";
import ScrollDown from "./Components/ScrollDown/ScrollDown";
import Sky from "./Components/Sky/Sky";
import Content from "./Components/Content/Content";
import Rig from "./Rig";

import Effects from "./Effects";
import { useCallback, useRef, useState, Suspense } from "react";

const maxY = 1420;
const minY = 0;
const debug = false;

const clamp = (value, min, max) => {
  if (value < min) return min;
  if (value > max) return max;
  return value;
};
export default function App() {
  const mouse = useRef([0, 0]);
  const scroll = useRef(0);
  const mouseRange = useRef(5);
  const progress = useRef(2);
  const [stage, setStage] = useState(0);

  const onMouseMove = (e) => {
    mouse.current = [
      2500 * (e.clientX / window.innerWidth - 0.5),
      1500 * (e.clientY / window.innerHeight - 0.5),
    ];
  };

  const onScroll = useCallback((e) => {
    if (progress.current < 1) return;
    setStage((prev) => {
      if (debug) {
        scroll.current = clamp(scroll.current + e.deltaY * 0.4, minY, maxY);
      } else if (prev > 2) {
        scroll.current = clamp(scroll.current + e.deltaY * 0.4, minY, maxY);
        return scroll.current >= maxY ? Infinity : 3;
      }
      return prev < 2 ? prev + 1 : prev;
    });
  }, []);

  return (
    <div className="container" onWheel={onScroll} onPointerMove={onMouseMove}>
      <Canvas camera={{ position: [0, 0, 700], fov: 40, far: 10000 }}>
        <Suspense fallback={null}>
          <Stats />
          {!debug && stage < 3 && (
            <>
              <Particles
                stage={stage}
                setStage={setStage}
                mouseRange={mouseRange}
                progress={progress}
              />
              <Effects />
            </>
          )}
          <Content scroll={scroll} stage={stage} debug={debug} />

          <Sky stage={stage} />
          {/* <OrbitControls /> */}
          <Rig mouse={mouse} mouseRange={mouseRange} />
        </Suspense>
      </Canvas>
      <Loader />
      {stage < Infinity && <ScrollDown />}
    </div>
  );
}
